<template>
  <div class="searchbar">
    <input type="text" v-model="inputText">
    <button 
    @click="sendRequestSearch"
    @key.enter="sendRequestSearch"
    >Search</button>
  </div>
</template>

<script>
export default {
  name: 'SearchBar',
  data(){
    return{
      inputText: "",
    }
  },
  methods :{
    sendRequestSearch(){
      this.$emit("searchRequest", this.inputText);
    }
  }
}
</script>

